.form {
  &-field {
    display: block;

    width: 100%;
    height: 48px;
    padding: 14px 16px;

    background-color: $form-field-bgc;
    border: 1px solid transparent;

    font-family: inherit;

    font-weight: 400;
    font-size: 14px;
    line-height: 20px;

    &::placeholder {
      color: $placeholders-color;
    }

    &:hover {
      border-color: $form-hover-color;

      color: $main-dark-color;
    }

    &:focus {
      border-color: $main-dark-color;
      outline: none;

      color: $main-dark-color;
    }

    &--text-area {
      height: 144px;
      resize: none;

      font-family: inherit;
    }
  }
}
